import uniCountdown from '@/components/uni-countdown/uni-countdown.vue'
export default {
	data() {
		return {
			id:'',
			disabled:true,
			sessionId:'',
			myData:{},
			user:[{
					price:'120',
					user:'luckydog',
					stat:'出价最高'
				}],
			d:'',
			h:'',
			m:'',
			n:'',
			myPrice:'',
			myTips:'',
			bidPrice:''
		}
	},
	onLoad(option){
		this.id = option.id
		this.sessionId = option.sessionId
		this.init()
	},
	
	methods: {
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/getAuctionById',
				data:{
					id:this.id,
					sessionId:this.sessionId
				},
				success:(res)=>{
					console.log(res)
					if(res.data.stat == 1){
						this.myData = res.data.data
						if(this.myData.stat == 1){
							this.myData.stat = '未拍卖'
							this.disabled = true
							this.clock()
						}else if(this.myData.stat == 2){
							this.myData.stat = '拍卖中'
							this.disabled = false
							this.clock2()
						}else if(this.myData.stat == 3){
							this.myData.stat = '已拍卖'
						}else{
							this.myData.stat = '已失效'
						}
					}else if(res.data.stat == 3 || res.data.stat == 4){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		},
		
		
		// 竞拍
		add(){
			uni.showModal({
				title:'提示',
				content:'是否参与竞拍？',
				success:(res)=>{
					this.$refs.popup.open()
					this.myPrice = ''
					this.bidPrice = this.myData.price * 1.05
					this.myTips = '不低于'+this.bidPrice
				}
			})
		},
		
		// 参与竞拍
		submit(){
			console.log(this.myPrice,this.sessionId)
			if(this.myPrice < this.bidPrice){
				uni.showToast({
					title:'加价不得低于'+this.bidPrice,
					icon:'none',
					duration:3000
				})
			}else{
				uni.request({
					url:'https://www.nssnail.com/wx/userBidding',
					method:'POST',
					data:{
						sessionId:this.sessionId,
						id:this.id,
						price:this.myPrice
					},
					success: (res) => {
						if(res.data.stat == 1){
							uni.showToast({
								title:res.data.message,
								icon:'success'
							})
							this.$refs.popup.close()
						}else if(res.data.stat == 3 || res.data.stat == 4){
							uni.showModal({
								title: '提示',
								content: '登录过期，请重新登录',
								success: function (res) {
									if (res.confirm) {
										uni.navigateTo({
											url:'../myInit/myInit'
										})
									}
								}
							});
						}else if(res.data.stat == 0){
							uni.showToast({
								title:res.data.message,
								icon:'none',
								duration:3000
							})
							this.$refs.popup.close()
						}
					}
				})
			}

		},
		
		// 时间戳倒计时  未开始
		clock(){
			var date = new Date();
			var now = date.getTime();//获得当前时间与1970年1月1日时间相差的毫秒数
			var star = this.myData.startTime
			var endDate = new Date(star); 
			var end = endDate.getTime();//结束时间与1970年1月1日时间相差的毫秒数
			var leftTime = end-now;//计算两日期之间相差的毫秒数
			if (leftTime >= 0) {
				this.d = Math.floor(leftTime/1000/60/60/24);
				this.h = Math.floor(leftTime/1000/60/60%24);  
				this.m = Math.floor(leftTime/1000/60%60);  
				this.s = Math.floor(leftTime/1000%60);
				console.log(this.d+'天'+this.h+'时'+this.m+'分'+this.s+'秒')
			}
		},
		
		// 已经开始
		clock2(){
			var date = new Date();
			var now = date.getTime();
			var star = this.myData.startTime
			var endDate = new Date(star); 
			var end = endDate.getTime(); 
			var leftTime = end-now;
			if (leftTime >= 0) {
				this.d = Math.floor(leftTime/1000/60/60/24);  
				this.h = this.myData.validTime+Math.floor(leftTime/1000/60/60%24);  
				this.m = Math.floor(leftTime/1000/60%60);  
				this.s = Math.floor(leftTime/1000%60);
				console.log(this.d+'天'+this.h+'时'+this.m+'分'+this.s+'秒')
			}
		}
	},
	
	components:{
		uniCountdown
	}
}
